import p5 from "p5";

const getRandomSymbol = () => {
  return String.fromCharCode(0x30a0 + Math.round(Math.random() * 90));
};

const getRandomNumber = (max: number, min: number = 10) => {
  return Math.floor(Math.random() * max) + min;
};

const Y_OFFSET = 21;

class Symbol {
  position: p5.Vector;
  velocity: p5.Vector;
  symbol: string;
  tail: Array<Symbol>;
  shouldGlow: boolean;
  shadeOffset: number;
  rainSpeed: number;
  constructor(p5: p5, positionX: number, isHead: boolean = true) {
    this.symbol = getRandomSymbol();
    this.initSymbol(p5, positionX);
    this.shadeOffset = 0;
    if (isHead) {
      this.velocity = p5.createVector(0, Math.random() * 2 + 1);
      //this.tail = new Array(getRandomNumber(p5.width / 100) + 10);
      this.tail = new Array(Math.floor(p5.height / 22));
      this.shadeOffset = getRandomNumber(255, 80);
      for (let i = 0; i < this.tail.length; i++) {
        this.tail[i] = new Symbol(p5, positionX, false);
      }
    }
  }

  initSymbol(p5: p5, positionX: number) {
    this.position = p5.createVector(positionX, p5.height - Y_OFFSET - 20); //-Math.random() * 500);
    this.shadeOffset = getRandomNumber(255, 80);
    this.rainSpeed = getRandomNumber(70, 20);
  }
  update(p5: p5) {
    //this.position.add(this.velocity);
    const changeSymbol = Math.random() > 0.99; //Math.random() > 0.95;

    // if (changeSymbol) {
    //   this.symbol = getRandomSymbol();
    // }
    // if (
    //   this.shadeOffset < 0 ||
    //   this.position.y > p5.height + this.tail.length * Y_OFFSET
    // ) {
    //   this.initSymbol(p5, this.position.x);
    // }
    //for (let i = 0; i < this.tail.length; i++) {
    for (let i = this.tail.length - 1; i >= 0; i--) {
      const calculatedY = this.position.y - Y_OFFSET * i - 20;
      //const alpha = 255 - 4 * i;
      if (p5.frameCount % 2 == 0) this.tail[i].shadeOffset -= 3;
      if (changeSymbol) {
        setTimeout(() => {
          //   glowSymbols(getRandomNumber(4, 2), this.tail, i, p5);
          this.tail[i].shouldGlow = true;
          this.tail[i].symbol = getRandomSymbol();
          this.tail[i].shadeOffset = getRandomNumber(255, 0);
        }, this.tail.length * 100 - i * this.rainSpeed);
      }
      if (this.tail[i].shouldGlow) {
        // i < 1 ||
        p5.stroke(200, 255, 200, this.tail[i].shadeOffset);
        p5.fill(200, 255, 200); //, this.tail[i].shadeOffset * 3
        this.tail[i].shouldGlow = false;
      } else {
        p5.noStroke();
        p5.fill(80, 255, 80, this.tail[i].shadeOffset); //, this.tail[i].shadeOffset
      }
      p5.text(this.tail[i].symbol, this.position.x, calculatedY);
    }
    if (p5.frameCount % 1000 === 0) {
      this.rainSpeed = getRandomNumber(50, 10);
    }
  }
  draw(p5: p5) {
    // p5.fill(180, 255, 180);
    // p5.text(this.symbol, this.position.x, this.position.y);
  }
}

export default Symbol;
